import React, { Component, Fragment } from "react";
import Courses from "../../../components/Courses/Courses";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import {
  Auth,
  courseStoreInterface,
  loadCourses,
  schoolLevelsStoreInterface,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  QuestionBankStoreInterface,
  setUploadedQuestions,
  questionTypesStoreInterface,
  loadQuestionTypes,
  loadQuestionBank,
  setSelectedCourse,
  PrepareExamsStoreInterface,
  addEditExamQuestion,
  applicationPeriodsStoreInterface,
  loadApplicationPeriods,
  setSelectedPeriod,
  System,
  PushPopupAlert,
  findExamDetails,
  loadExamsListByPeriod,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { course } from "../../../interfaces/course";
import {
  QuestionInterface,
  uploadQuestionsResponse,
} from "../../../interfaces/question";
import NoResult, {
  NoResultThemes,
} from "../../../components/NoResult/NoResult";
import Questions from "../../../components/Questions/Questions";
import Loading from "../../../components/Loading/Loading";
import QuestionItem from "../../../components/Questions/QuestionItem";
import { ApplicationPeriodGet } from "../../../interfaces/application";
import ApplicationPeriods from "../../../components/ApplicationPeriods/ApplicationPeriods";
import { API } from "../../../utils/api";
import axios from "axios";
import Alert, { AlertInterface } from "../../../components/Alert/Alert";
import {
  ExamItemResponse,
  EXAM_QUESTIONS_NUMBERING,
  PrepareExamStatus,
  QuestionNumberFormat,
} from "../../../interfaces/prepare_exam";
import { Redirect } from "react-router-dom";
import { PopupAlertData } from "../../../components/Alert/Popup";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import EditQuestion from "../../QuestionBank/AddQuestion/UploadQuestion/EditQuestion/EditQuestion";
import { MdAdd } from "react-icons/md";
import ReactQuill from "react-quill";
